<template>
  <div class="body" :style="note">
    <div class="btn">
      <div
        class="button"
        @mouseover="mouseover(0)"
        @mouseleave="mouseleave(0)"
        @click="btn(0)"
      >
        <img class="buton" src="../../image/home/kuang.png" alt="" />
        <img src="../../image/home/jinrong.png" alt="" />
        <span>金融服务大屏</span>
      </div>
      <div
        class="button"
        @mouseover="mouseover(1)"
        @mouseleave="mouseleave(1)"
        @click="btn(1)"
      >
        <img class="buton" src="../../image/home/kuang.png" alt="" />
        <img src="../../image/home/xinyong.png" alt="" />
        <span>信用评价大屏</span>
      </div>
      <div
        class="button"
        @mouseover="mouseover(2)"
        @mouseleave="mouseleave(2)"
        @click="btn(2)"
      >
        <img class="buton" src="../../image/home/kuang.png" alt="" />
        <img src="../../image/home/fengxian.png" alt="" />
        <span>风险防控大屏</span>
      </div>
      <div
        class="button"
        @mouseover="mouseover(3)"
        @mouseleave="mouseleave(3)"
        @click="btn(3)"
      >
        <img class="buton" src="../../image/home/kuang.png" alt="" />
        <img src="../../image/home/chanye.png" alt="" />
        <span>产业支撑大屏</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      note: {
        backgroundImage: "url(" + require("../../image/home/bg.png") + ")",
      },
    };
  },
  created() {},
  /*
    加载页面后
  */
  mounted() {},
  /*
    存放函数内容
  */
  methods: {
    // 鼠标移入事件
    mouseover(e) {
      let index = e;
      document.getElementsByClassName("buton")[
        index
      ].src = require("../../image/home/chooseKuang.png");
    },

    // 鼠标移出事件
    mouseleave(e) {
      let index = e;
      document.getElementsByClassName("buton")[
        index
      ].src = require("../../image/home/kuang.png");
    },

    btn(e) {
      let id = e;
      let aflag = "";
      switch (id) {
        case 0:
          aflag = "financial";
          break;
        case 1:
          aflag = "credit";
          break;
        case 2:
          aflag = "risk";
          break;
        case 3:
          aflag = "industry";
          break;
        default:
          break;
      }
      const { href } = this.$router.resolve({
        name: aflag,
        params: {
          id: id,
        },
      });
      window.open(href, "_blank");
    },
  },
};
</script>

