import { createRouter, createWebHashHistory } from 'vue-router'
// createWebHistory history模式
// createWebHashHistory hash模式
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      // 页面标题title
      title: '嵩县金融服务系统'
    }
  },
  {
    path: '/financial',
    name: 'financial',
    component: () => import(/* webpackChunkName: "about" */ '../views/financial.vue'),
    meta: {
      title: '嵩县金融扶贫服务系统'
    }
  },
  {
    path: '/credit',
    name: 'credit',
    component: () => import(/* webpackChunkName: "about" */ '../views/credit.vue'),
    meta: {
      title: '嵩县信用评价系统'
    }
  },
  {
    path: '/risk',
    name: 'risk',
    component: () => import(/* webpackChunkName: "about" */ '../views/risk.vue'),
    meta: {
      title: '嵩县风险防控系统'
    }
  },
  {
    path: '/industry',
    name: 'industry',
    component: () => import(/* webpackChunkName: "about" */ '../views/industry.vue'),
    meta: {
      title: '嵩县产业支撑系统'
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
